import React from 'react';
import Toolbar from '@mui/material/Toolbar';
import Badge from '@mui/material/Badge';
import Typography from '@mui/material/Typography';
import AppBar from '@mui/material/AppBar';
import { styled } from '@mui/material/styles';
import { Environments, getEnvironment, getVersion } from '../util/config';

const StyledAppBar = styled(AppBar)(({ theme }) => ({
    flexShrink: 0,
    zIndex: theme.zIndex.drawer + 1,

    '> .MuiBadge-root': {
        height: 30,
    },
}));

const StyledBadge = styled(Badge)(({ theme }) => ({
    '& .MuiBadge-badge': {
        right: -20,
        top: 15,
        border: `2px solid ${theme.palette.background.paper}`,
        padding: '0 4px',
    },
}));

export default function WindowHeader() {
    const environment = getEnvironment();
    const getBadgeColour = (env: Environments) => {
        switch (env) {
            case Environments.DEV:
                return 'error';
            case Environments.UAT:
                return 'warning';
            case Environments.PRD:
                return 'success';
        }
    };

    return (
        <StyledAppBar position="relative">
            <Toolbar variant="dense">
                <StyledBadge
                    badgeContent={environment === Environments.PRD ? getVersion() : environment}
                    color={getBadgeColour(environment)}
                >
                    <Typography variant="h6" color="inherit" component="div">
                        Rail Map Generator
                    </Typography>
                </StyledBadge>
            </Toolbar>
        </StyledAppBar>
    );
}
