import React, { useEffect, useState } from 'react';
import Drawer from '@mui/material/Drawer';
import Alert from '@mui/material/Alert';
import Box from '@mui/material/Box';
import { useAppSelector } from '../redux';
import TextField from '@mui/material/TextField';
import { setSelectedStation } from '../redux/app/action';
import { useDispatch } from 'react-redux';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';

const StyledDrawer = styled(Drawer)(({ open }) => ({
    width: open ? 320 : 0,
    flexShrink: 0,
    position: 'relative',
    '> .MuiDrawer-paper': { width: 320, position: 'relative', backgroundColor: 'unset' },
}));

const StyledAlert = styled(Alert)(() => ({
    paddingTop: 0,
    paddingBottom: 0,
}));

export default function SidePanel() {
    const dispatch = useDispatch();

    const [isOpen, setIsOpen] = useState(false);

    const selectedStation = useAppSelector(state => state.app.selectedStation);
    const { name } = useAppSelector(state => state.param.stn_list[selectedStation]);

    useEffect(() => {
        if (!['linestart', 'lineend'].includes(selectedStation)) {
            setIsOpen(true);
        }
    }, [selectedStation]);

    const handleClose = () => {
        setIsOpen(false);
        setTimeout(() => {
            dispatch(setSelectedStation('linestart'));
        }, 200);
    };

    return (
        <Paper>
            <StyledDrawer variant="persistent" anchor="right" open={isOpen}>
                <StyledAlert severity="info" onClose={handleClose}>
                    {name[0] + ' - ' + name[1]}
                </StyledAlert>

                <Box>
                    <TextField label="Chinese name" variant="standard" size="small" value={name[0]} />
                    <TextField label="English name" variant="standard" size="small" value={name[1]} />
                </Box>
            </StyledDrawer>
        </Paper>
    );
}
