import React, { ReactElement } from 'react';
import TableContainer from '@mui/material/TableContainer';
import Table from '@mui/material/Table';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import TableBody from '@mui/material/TableBody';

type valueField<T> = {
    label: string;
    key: keyof T;
};

type customField<T> = {
    label: string;
    displayHandler: (d: T) => ReactElement | string | number;
};

export type DataTableFieldType<T> = valueField<T> | customField<T>;
export type DataTableDataType = { id: string; isSelected: boolean } & { [key: string]: any };

interface StationDisplayDataProps<T> {
    data: T[];
    fields: Array<DataTableFieldType<T>>;
}

export default function DataTable<T extends DataTableDataType>(props: StationDisplayDataProps<T>) {
    const { data, fields } = props;

    const pageData = data.map(d => ({
        data: d,
        display: fields.reduce<{ [key: string]: { value: any } }>((acc, cur) => {
            const value = (cur as any).key ? (d[(cur as any).key] || '').toString() : (cur as any).displayHandler(d);
            acc[cur.label] = { value };
            return acc;
        }, {}),
    }));

    return (
        <TableContainer>
            <Table stickyHeader size="small">
                <TableHead>
                    <TableRow>
                        {fields.map((field, i) => (
                            <TableCell key={i}>{field.label}</TableCell>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {pageData.map(item => (
                        <TableRow key={item.data.id} tabIndex={-1} selected={item.data.isSelected} hover>
                            {fields.map((field, i) => (
                                <TableCell key={item.data.id + '-' + i}>{item.display[field.label].value}</TableCell>
                            ))}
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
