import React from 'react';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import { setCurrentStation } from '../../redux/param/action';
import { setSelectedStation } from '../../redux/app/action';
import { useDispatch } from 'react-redux';
import { styled } from '@mui/material/styles';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import MyLocationIcon from '@mui/icons-material/MyLocation';

const ActionsWrapper = styled(Box)(() => ({
    '> .MuiIconButton-root': { marginRight: 5 },
}));

interface TableRowActionsProps {
    stationId: string;
}

export default function TableRowActions(props: TableRowActionsProps) {
    const { stationId } = props;

    const dispatch = useDispatch();

    const handleDelete = () => {};

    return (
        <ActionsWrapper>
            <IconButton size="small" onClick={() => dispatch(setCurrentStation(stationId))}>
                <MyLocationIcon fontSize="inherit" />
            </IconButton>
            <IconButton size="small" onClick={() => dispatch(setSelectedStation(stationId))}>
                <EditIcon fontSize="inherit" />
            </IconButton>
            <IconButton size="small" onClick={handleDelete}>
                <DeleteIcon fontSize="inherit" />
            </IconButton>
        </ActionsWrapper>
    );
}
